"use client";

import { useEffect, useState } from "react";
import { useAccount, useBalance, useChainId, useConnect, useDisconnect } from "wagmi";
import { monadTestnet } from "../lib/wagmi";

export default function WalletStatus() {
  const [mounted, setMounted] = useState(false);
  const { address, isConnected } = useAccount();
  const { data: balance } = useBalance({
    address,
    chainId: monadTestnet.id,
    query: {
      enabled: Boolean(address)
    }
  });
  const chainId = useChainId();
  const { connect, connectors, isPending } = useConnect();
  const { disconnect } = useDisconnect();

  const isCorrectChain = chainId === monadTestnet.id;

  useEffect(() => {
    setMounted(true);
  }, []);

  if (!mounted) {
    return (
      <div className="card">
        <h3>Wallet</h3>
        <p className="muted">Loading wallet status...</p>
      </div>
    );
  }

  return (
    <div className="card">
      <h3>Wallet</h3>
      {isConnected ? (
        <>
          <p className="muted">
            Connected: <span className="mono">{address}</span>
          </p>
          <p className="muted">
            Balance:{" "}
            <span className="mono">
              {balance?.formatted
                ? `${Number(balance.formatted).toFixed(4)} ${balance.symbol || "MON"}`
                : "Loading..."}
            </span>
          </p>
          <p className={isCorrectChain ? "success" : "error"}>
            {isCorrectChain ? "Monad Testnet" : `Wrong network (${chainId})`}
          </p>
          <button className="secondary" onClick={() => disconnect()}>
            Disconnect
          </button>
        </>
      ) : (
        <>
          <p className="muted">Connect your wallet to view challenges.</p>
          <button
            onClick={() => connect({ connector: connectors[0] })}
            disabled={isPending}
          >
            Connect Wallet
          </button>
        </>
      )}
    </div>
  );
}
